<template>
  <div class="hello">
    <ul class="videolist-wrap">

      <li v-for="v in videos" :key="v.id" @click="handleitemclick(v)"><img :src="v.iaddr" :hls="v.vaddr"/><span>{{v.id}}</span></li>

    </ul>
    <div>
      <span @click="goprev">Prev</span><span>{{currentpage}}</span><span @click="gonext">Next</span>
    </div>
  </div>
  <div>
    <video ref="videoPlayer" class="video-js"></video>
  </div>
</template>

<script>
import videojs from 'video.js';
import 'video.js/dist/video-js.css';
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data(){
    return {
      totalvideos:85801,
      player:null,
      currentpage:1,
      imgbaseurl:"https://hp.cloudfront.xyz/tmb/",
      videobaseurl:"https://hp.cloudfront.xyz/hls/mp4/"
    }
  },
  computed: {
    videos(){
      var tmpvideos=[];

      for(var i=1;i<=20;i++){
        var tmpid=this.totalvideos-(this.currentpage-1)*20-i;
        var tmpvideo={
          id:tmpid,
          iaddr:this.imgbaseurl+tmpid+`/preview.jpg`,
          vaddr:this.videobaseurl+tmpid+`.mp4/index.m3u8`
        };
        tmpvideos.push(tmpvideo);
      }
      return tmpvideos;
    }
  },
  mounted(){
    this.player = videojs(this.$refs.videoPlayer, {
      html5: true,
      autoplay: false,
				controls: true,
        width:"300px",
        height:"300px",
				sources: [
					{
						src:"https://hp.cloudfront.xyz/hls/mp4/85800.mp4/index.m3u8",
						type: "application/x-mpegURL"
					}
				]
    }, function onPlayerReady() {
            console.log('onPlayerReady', this);
        })
  },
  methods:{
    goprev(){
      this.currentpage=this.currentpage-1;
    },
    gonext(){
      this.currentpage=this.currentpage+1;
    },
    handleitemclick(v){
      this.player.src([{
        src:v.vaddr,
        type: "application/x-mpegURL",
        withCredentials: false
      }]);
       console.log(v);
    }
  }
}
</script>

